.main {
  width: 100%;
  height: 500px;
  background: bisque;

}

.click {
  width: 50px;
  height: 50px;
  background: #333;
  transition: 0.3s all;
  color:#fff;
  display:flex;
  align-items: center;
  align-content: center;
  &:hover{
    width:200px;
    background: red;
  }
}